import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
} from '@material-ui/core';
import { AppPagination, Spinner } from 'components';
import { Delete } from '@material-ui/icons';
import { useStyles } from './styles';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import clsx from 'clsx';
import style from '../Customer.module.css';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const AdminCooperate = () => {
  const classes = useStyles();
  const {
    getTransportCooperate,
    getWarehouseCooperate,
    getCooperateOther,
    exportTransport,
    exportWarehouse,
    exportOther,
  } = useGlobalContext();
  const [arrayCooperates, setArrayCooperates] = useState();
  const [typeCooperate, setTypeCooperate] = useState(0);
  const [loading, setLoading] = useState(false);
  const [pageCurrent, setPageCurrent] = useState(1);
  const [totalPage, setTotalPage] = useState();
  const [form, setForm] = useState({
    from: '',
    to: '',
  });

  const getTime = (e) => {
    const date = new Date(e);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day}/${month}/${year}`;
  };
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const setFrom = (e) => {
    setStartDate(e);
    setForm({ ...form, from: getTime(e) });
  };
  const setTo = (e) => {
    setEndDate(e);
    setForm({ ...form, to: getTime(e) });
  };
  const handleExportCooperate = async () => {
    let res;
    if (typeCooperate === 0) {
      res = await exportTransport(form);
    } else if (typeCooperate === 1) {
      res = await exportWarehouse(form);
    } else {
      res = await exportOther(form);
    }
    const url = window.URL.createObjectURL(new Blob([res.data]));
    const link = document.createElement('a');
    link.href = url;
    link.setAttribute('download', 'cooperate.xlsx');
    document.body.appendChild(link);
    link.click();
    link.parentNode.removeChild(link);
  };
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      let res;
      switch (typeCooperate) {
        case 0:
          res = await getTransportCooperate(pageCurrent);
          break;
        case 1:
          res = await getWarehouseCooperate(pageCurrent);
          break;
        case 2:
          res = await getCooperateOther(pageCurrent);
          break;

        default:
          break;
      }
      if (res.success) {
        if (res.total % 6 !== 0) {
          setTotalPage(Math.floor(res.total / 6) + 1);
        } else {
          setTotalPage(res.total / 6);
        }
        setArrayCooperates(res.data);
      } else {
        setArrayCooperates([]);
      }
      setLoading(false);
    };
    run();
  }, [pageCurrent, typeCooperate]);
  return (
    <AdminLayout>
      {loading && <Spinner />}
      <div className={style.boxHeader}>
        <Button
          variant="outlined"
          className={clsx(classes.buttonType, {
            [classes.active]: typeCooperate === 0,
          })}
          onClick={() => setTypeCooperate(0)}
        >
          Vận tải
        </Button>
        <Button
          variant="outlined"
          className={clsx(classes.buttonType, {
            [classes.active]: typeCooperate === 1,
          })}
          onClick={() => setTypeCooperate(1)}
        >
          Kho bãi
        </Button>
        <Button
          variant="outlined"
          className={classes.buttonType}
          className={clsx(classes.buttonType, {
            [classes.active]: typeCooperate === 2,
          })}
          onClick={() => setTypeCooperate(2)}
        >
          Khác
        </Button>
        <div className={style.formExport}>
          <label htmlFor="from">From</label>
          <DatePicker selected={startDate} onChange={setFrom} />
        </div>
        <div className={style.formExport}>
          <label htmlFor="to">To</label>
          <DatePicker selected={endDate} onChange={setTo} />
        </div>
        <Button variant="outlined" onClick={handleExportCooperate}>
          Export
        </Button>
      </div>
      <TableContainer className={classes.tableCooperate} component={Paper}>
        <Table aria-label="caption table">
          <TableHead>
            {typeCooperate === 0 && (
              <TableRow>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Ngày
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Họ tên/Tên doanh nghiệp
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Địa chỉ
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Số điện thoại
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Email
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Số lượng xe
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Loại xe
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Trọng tải xe
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Kích thước thùng xe
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Ghi chú
                </TableCell>
                <TableCell
                  className={style.textOver}
                  width="100px"
                  align="left"
                >
                  Loại hợp đồng
                </TableCell>
              </TableRow>
            )}
            {typeCooperate === 1 && (
              <TableRow>
                <TableCell width="10%" className={style.textOver} align="left">
                  Ngày
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Họ tên/Tên doanh nghiệp
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Địa chỉ
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Số điện thoại
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Email
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Số lượng kho
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Địa điểm
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Diện tích kho
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Ghi chú
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Loại hợp đồng
                </TableCell>
              </TableRow>
            )}
            {typeCooperate === 2 && (
              <TableRow>
                <TableCell width="10%" className={style.textOver} align="left">
                  Ngày
                </TableCell>
                <TableCell width="15%" className={style.textOver} align="left">
                  Họ tên/Tên doanh nghiệp
                </TableCell>
                <TableCell width="15%" className={style.textOver} align="left">
                  Địa chỉ
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Số điện thoại
                </TableCell>
                <TableCell width="15%" className={style.textOver} align="left">
                  Email
                </TableCell>
                <TableCell width="25%" className={style.textOver} align="left">
                  Ghi chú
                </TableCell>
                <TableCell width="10%" className={style.textOver} align="left">
                  Loại hợp đồng
                </TableCell>
              </TableRow>
            )}
          </TableHead>
          <TableBody>
            {typeCooperate === 0 &&
              arrayCooperates?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {getTime(item.createdAt)}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.name}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.address}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.phone}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.email}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.numberVehicle}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.rangeVehicle}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.vehicleWeight}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.dementionTruck}
                  </TableCell>
                  <TableCell
                    width="100px"
                    align="left"
                    dangerouslySetInnerHTML={{ __html: item.other }}
                  ></TableCell>
                  <TableCell
                    className={style.textOver}
                    width="100px"
                    align="left"
                  >
                    {item.type}
                  </TableCell>
                </TableRow>
              ))}
            {typeCooperate === 1 &&
              arrayCooperates?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {getTime(item.createdAt)}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.name}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.address}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.phone}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.email}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.numberWarehouse}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.location}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.warehouseArea}
                  </TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                    dangerouslySetInnerHTML={{ __html: item.other }}
                  ></TableCell>
                  <TableCell
                    width="10%"
                    className={style.textOver}
                    align="left"
                  >
                    {item.type}
                  </TableCell>
                </TableRow>
              ))}
            {typeCooperate === 2 &&
              arrayCooperates?.map((item, index) => (
                <TableRow
                  key={index}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell
                    className={style.textOver}
                    width="10%"
                    align="left"
                  >
                    {getTime(item.createdAt)}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="15%"
                    align="left"
                  >
                    {item.name}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="15%"
                    align="left"
                  >
                    {item.address}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="10%"
                    align="left"
                  >
                    {item.phone}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="15%"
                    align="left"
                  >
                    {item.email}
                  </TableCell>
                  <TableCell
                    className={style.textOver}
                    width="25%"
                    align="left"
                    dangerouslySetInnerHTML={{ __html: item.other }}
                  ></TableCell>
                  <TableCell
                    className={style.textOver}
                    width="10%"
                    align="left"
                  >
                    {item.type}
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <AppPagination
        totalPage={totalPage}
        onChange={(page) => setPageCurrent(page)}
      />
    </AdminLayout>
  );
};

export default AdminCooperate;

export const ARRAY_COOPERATES = [
  {
    name: 'Cty A',
    address: '123 Ton That Tung, Cau giay, Ha Noi',
    phone: '012345678',
    email: 'a@gmail.com',
    number_vehicle: '123',
    type_vehicle: '123',
    weight_vehicle: '123',
    width_vehicle: '123',
    number_warehouse: '123',
    location_warehouse: '123',
    area_warehouse: '123',
    content: '123',
    type_customer: '1',
    type_cooperate: '1  ',
  },
];
