import React from 'react';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Link } from 'react-router-dom';
import { useStyles } from '../../styles';
import { memo } from 'react';
import { MENU_ROUTES } from '../../route';
import { useGlobalContext } from 'store';
import clsx from 'clsx';

const NavMenu = ({ open, setOpen }) => {
  const { activeMenuAdmin, setActiveMenuAdmin } = useGlobalContext();
  const classes = useStyles();
  return (
    <Drawer
      className={classes.drawer}
      variant="persistent"
      anchor="left"
      open={open}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.drawerHeader}></div>
      <Divider />
      <List>
        {MENU_ROUTES.map((route, index) => (
          <ListItem
            selected={index === activeMenuAdmin}
            onClick={() => setActiveMenuAdmin(index)}
            className={clsx(classes.menuItem)}
            button
            key={index}
          >
            <Link className={clsx(classes.menuLink)} to={route.path}>
              <ListItemIcon>{route.icon}</ListItemIcon>
              <ListItemText>{route.title} </ListItemText>
            </Link>
          </ListItem>
        ))}
      </List>
      <Divider />
    </Drawer>
  );
};

export default memo(NavMenu);
