import React, { useState, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import StringFormat from 'string-format';
import clsx from 'clsx';
import { HomeLayout } from 'layouts';
import { useStyles } from './styles';
import { PathConstant } from 'const';
import { ImageContainer, Spinner } from 'components';
import { Link } from 'react-router-dom';
import { LangConstant } from 'const';
import { useTranslation } from 'react-i18next';
import style from './ServiceAnimation.module.css';
import { useGlobalContext } from 'store';

const Service = () => {
  const classes = useStyles();
  const [loading, setLoading] = useState(false);
  const [arrayImg, setArrayImg] = useState();
  const { getDataConfig, PAGE_KEY } = useGlobalContext();
  const { t: getLabel, i18n } = useTranslation();
  const [arrayServices, setArrayServices] = useState();
  const origin = window.location.origin;
  useEffect(() => {
    if (document.documentElement.clientWidth < 850) {
      setArrayServices(ARRAY_SERVICES_MOBILE);
    } else {
      setArrayServices(ARRAY_SERVICES);
    }
  }, []);
  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getDataConfig(PAGE_KEY.servicesAll, i18n.language);
      if (res.success) {
        setArrayImg(res.data[0].value);
        setLoading(false);
      } else {
        setArrayImg([]);
        setLoading(false);
      }
    };
    run();
  }, [i18n.language]);
  return loading ? (
    <Spinner />
  ) : (
    <HomeLayout typeFooter="copyright">
      <ImageContainer className={classes.root} src={arrayImg?.imgBackGround}>
        <div className={classes.container}>
          {arrayServices?.map((item, index) => (
            <Link
              key={index}
              to={StringFormat(PathConstant.SERVICE_ID, index)}
              className={clsx(classes.boxService, style.boxService)}
            >
              <ImageContainer
                src={arrayImg[`services${index}`]}
                className={classes.imageService}
                cover={true}
              ></ImageContainer>
              <div className={classes.boxContent}>
                <div className={clsx('root-center', classes.noteContainer)}>
                  <Typography className={classes.note}>❖</Typography>
                </div>

                <div className={classes.content}>
                  <Typography
                    className={classes.textContent}
                    dangerouslySetInnerHTML={{ __html: getLabel(item.name) }}
                  ></Typography>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </ImageContainer>
    </HomeLayout>
  );
};

export default Service;

